@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;700&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Roboto", sans-serif;
}

body {
  background-color: black;
}

.make-it-center-please {
  display: flex;
  justify-content: center;
  align-items: center;
}

.box {
  background-color: #f9f9f9;
  margin: auto;
  height: 300px;
  width: 700px;
  position: relative;
}
.box__top {
  display: inline-flex;
  margin-top: 15px;
  margin-left: 15px;
}
.box__top p {
  margin-left: 5px;
  margin-top: auto;
  color: #236c62;
  font-size: 18;
  font-weight: bold;
}
.box__bottom {
  display: flex;
  margin-top: 7px;
}
.box__bottom--left {
  margin-left: 18px;
  width: 300px;
}
.box__bottom--left img {
  width: 100%;
  height: auto;
}
.box__bottom--left p {
  margin-top: 18px;
  font-size: 10px;
  color: #656565;
}
.box__bottom--right {
  margin-left: 18px;
  width: 300px;
}

.slider {
  display: flex;
  overflow: hidden;
}

.slide {
  flex: 0 0 100%;
  transition: transform 0.5s ease-in-out;
}
.slide-content {
  margin-top: 20px;
  margin-bottom: 15px;
  margin-left: 15px;
}
.slide__top {
  display: flex;
}
.slide__top--left img {
  height: 123px;
  width: 123px;
}
.slide__top--right {
  margin-left: 12px;
  width: 140px;
}
.slide__top--right img {
  width: 82px;
  height: 30px;
}
.slide__top--right h4 {
  font-size: 14px;
  color: #222222;
}
.slide__top--right p {
  font-size: 13px;
  color: #474747;
}
.slide__title {
  margin-top: 6px;
  margin-bottom: 5px;
}
.slide__bottom {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 25px;
}
.slide__button {
  width: 100px;
  height: 25px;
  border-radius: 10px;
  background: var(--dr-green-2, radial-gradient(259.48% 104.13% at 45.83% 54.55%, rgba(11, 24, 27, 0.5) 0%, rgba(24, 121, 104, 0.5) 51.28%, rgba(0, 200, 164, 0.5) 100%), #20c8a7);
  font-size: 10px;
  font-weight: 900;
  color: #ffffff;
  border: none;
  box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
}

.pagination {
  display: flex;
  justify-content: center;
}

.pagination span {
  width: 6px;
  height: 6px;
  background-color: #bbb;
  border-radius: 50%;
  margin: 0 4px;
  display: inline-block;
  cursor: pointer;
}

.pagination span.active {
  background-color: #656565;
}

.green-bar {
  width: 100%;
  height: 115px;
  background: radial-gradient(259.48% 104.13% at 45.83% 54.55%, rgba(11, 24, 27, 0.5) 0%, rgba(24, 121, 104, 0.5) 51.28%, rgba(0, 200, 164, 0.5) 100%);
  position: absolute;
  z-index: -1;
  bottom: 80px;
}